import React, {useEffect, useState} from "react";
import axios from "axios";
import { Category } from "../utils/types";
import { ContextModule } from 'webpack';

interface CatalogMenuProps {
    toggleCatalogMenu: () => void;
    onSelectCategory: (category: Category | 'all') => void;
}

const importAll = (r: ContextModule) => r.keys().map(x => ({[x.replace('./', '')]: r(x)}));
const images = importAll(require.context('../assets/icons', false, /.(png|jpe?g|svg)$/));

let getImgSrc = (imgPath: string) => {
    let img = images.find(x => Object.keys(x)[0] === imgPath);
    return img ? Object.values(img)[0] : null;
}

export default function CatalogMenu({ toggleCatalogMenu, onSelectCategory }: CatalogMenuProps): JSX.Element {
    const [categories, setCategories] = useState<Category[]>([]);
  
    useEffect(() => {
        const fetchCategories = async () => {
        try {
            const response = await axios.get('http://127.0.0.1:8000/api/get/category');
            setCategories(response.data.categories);
            console.log(response.data);
        } catch (error) {
            console.error(`There was an error retrieving the data: ${error}`);
        }
        };

        fetchCategories();
    }, []);
    
    return(
        <>
            <div className="background-blackout" onClick={toggleCatalogMenu}></div>
            <ul className="catalog-menu">
                {categories.map((category) => (
                <li key={category.id} className="catalog-menu__point-li" onClick={() => onSelectCategory(category)}>
                    <img className="catalog-menu__category-icon" src={getImgSrc(category.image)} alt={category.title} />
                    {category.title}
                </li>
                ))}
            </ul>
        </>
    )
}